<template>
  <div class="home">
    <Header></Header>
    <div class="country-list">
      <div class="searchFilter">
        <div class="search">
          <i class="fas fa-search"></i>
          <input type="text" placeholder="Search for a country" v-model="search" @keyup="findData">
        </div>
        <div class="filter">
          <div class="selector" @click="showMenu=!showMenu">
            <span class="value">{{region||'Filter By Region'}}</span>
            <i v-show="!showMenu" class="fas fa-angle-down"></i>
            <i v-show="showMenu" class="fas fa-angle-up"></i>
          </div>
          <div class="options" v-if="showMenu">
            <span class="option" data-value="All" @click="applyFilter">All</span>
            <span class="option" data-value="Africa" @click="applyFilter">Africa</span>
            <span class="option" data-value="American" @click="applyFilter">American</span>
            <span class="option" data-value="Asia" @click="applyFilter">Asia</span>
            <span class="option" data-value="Europe" @click="applyFilter">Europe</span>
            <span class="option" data-value="Oceania" @click="applyFilter">Oceania</span>
          </div>
        </div>
      </div>
    </div>
    <div class="loader" v-if="loading">
        <i class="fas fa-undo"></i>
    </div>
    <div class="error" v-if="showError">
        <h3 style="color:grey">Not found</h3>
    </div>
    <div class="country-show">
      
      <Country v-for="country in result" :key="country.name" :country="country"></Country>
    </div>
  </div>
</template>

<script>
import HelloWorld from '@/components/HelloWorld.vue'
import {mapState} from 'vuex';
import Header from '@/components/Header.vue';
import Country from '@/components/Country.vue';

export default {
  name: 'Home',
  data:function(){
    return {
      showMenu:false,
      region:'',
      countries:[],
      result:[],
      loading:true,
      search:'',
      showError:false
    }
  },
  components: {
    HelloWorld,
    Header,
    Country
  },
  computed:{
    ...mapState([
      'mode'
    ])
  },
  methods:{
   applyFilter:function(e){
     this.region=e.target.getAttribute('data-value');
     this.showMenu=false;
   },
   findData(){
     if(this.search==''){
       if(this.region=='All'){
         this.result=this.countries;
         this.showError=false;
       }else{
         this.result=this.countries.filter((country)=>{
           return country.region.toLowerCase().search(this.region.toLowerCase())!=-1;
          })
          this.showError=false;
       }
     }else{
       if(this.region=='All'){
         this.result=this.countries.filter((country)=>{
            return country.name.toLowerCase().search(this.search.toLowerCase())!=-1;
          }
        )

         }else{
            this.result=this.countries.filter((country)=>{
            return country.name.toLowerCase().search(this.search.toLowerCase())!=-1;
              }
            ).filter((country)=>{
              return country.region.toLowerCase().search(this.region.toLowerCase())!=-1;
            })
         }
        this.showError=false;
     }
     if(this.result.length==0){
       this.showError=true;
     }
   }
  },
  watch:{
    region:function(val){
     this.findData()
    }
  },
  created:async function(){
    await fetch('https://restcountries.eu/rest/v2/all').then((res)=>res.json())
    .then(data=>{
      this.countries=data;
      this.result=this.countries;
    })
    this.loading=false;
    console.log(this.countries)
  }
}
</script>
