<template>
  <router-view>
    
  </router-view>
</template>
<script>
import { mapState } from 'vuex'
export default {
  computed:{
    ...mapState([
      'mode'
    ])
  },
  watch:{
    mode:function(val){
      var root=document.documentElement;
      if(val=='night'){
        root.style.setProperty('--header-color','#121219')
        root.style.setProperty('--background-color','#121212')
        root.style.setProperty('--header-text','white')
        root.style.setProperty('--secondary-text','white')
        root.style.setProperty('--ternary-text','white')
      }else{
        root.style.setProperty('--header-color','white')
        root.style.setProperty('--background-color','rgb(243, 243, 243)')
        root.style.setProperty('--header-text','black')
        root.style.setProperty('--secondary-text','rgb(77, 77, 77)')
        root.style.setProperty('--ternary-text','rgb(119, 119, 119)')
      }
    }
  }
}
</script>
<style lang="scss">
:root{
  --header-color:white;
  --background-color:rgb(243, 243, 243);
  --header-text:black;
  --secondary-text:rgb(77, 77, 77);
  --ternary-text:rgb(119, 119, 119);
}
*{
  margin:0;
  padding:0;
  box-sizing:0;
  font-family: 'nunito',sans-serif;
  transition:0.3s all;
}
body{
  background:var(--background-color);
}
</style>
