<template>
    <div class="header">
        <h1>Where in the world ?</h1>
        <div class="mode-switch" @click="toggleMode">
            <i class="far fa-moon"></i>
            <span v-if="mode=='light'">Dark Mode</span>
            <span v-else>Light Mode</span>
        </div>
    </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex'
export default {
    methods:{
        ...mapMutations([
            'toggleMode'
        ])
    },
    computed:{
        ...mapState([
            'mode'
        ])
    }
}
</script>
<style lang="scss">
    .header{
        background-color:var(--header-color);
        display:flex;
        align-items:center;
        justify-content:space-between;
        padding:15px 100px;
        h1{
            color:var(--header-text)
        }
        .mode-switch{
            color:var(--header-text);
            cursor:pointer;
            display:flex;
            align-items: center;
            i{
                margin-right:10px;
            }
        }
    }
    //tablet
   @media screen and (max-width:1024px) and (min-width:768px){
       .header{
           padding:15px 50px;
           h1{
               font-size: 20px;
           }
           .mode-switch{
               font-size:15px;
           }
       }
   }
   //mobile
   @media screen and (max-width:767px){
       .header{
           padding:10px 20px;
           h1{
               font-size: 15px;
            }
            .mode-switch{
                font-size:10px;
            }
       }
   }
</style>