<template>
  <div class="about">
    <!-- <h1>{{$route.params.country}}</h1> -->
    <Header></Header>
    <div class="back" @click="$router.go(-1)">
      <span>Back</span>
    </div>
    <div class="error" v-if="showError">
        <h3 style="color:grey">Not found</h3>
    </div>
    <div class="country-info" v-if="country.length!=0">
      <div class="flag">
        <img :src="country.flag" alt="">
      </div>
      <div class="info">
        <h1>{{country.name}}</h1>
        <div class="subinfo">
          <div class="left">
            <h4>Native Name : <span>{{country.nativeName}}</span></h4>
            <h4>Population : <span>{{country.population}}</span></h4>
            <h4>Region : <span>{{country.region}}</span></h4>
            <h4>Subregion : <span>{{country.subregion}}</span></h4>
            <h4>Capital : <span>{{country.capital}}</span></h4>
          </div>
          <div class="right">
            <h4>Top Level Domain : <span>{{country.topLevelDomain.join(',')}}</span></h4>
            <h4>Currencies : <span>{{country.currencies[0].code}}</span></h4>
            <h4>Languages : <span>{{country.languages[0].name}}</span></h4>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header.vue';

export default {
  name:'About',
  components:{
    Header,
  },
  data:function(){
    return {
      country:[],
      showError:false
    }
  },
  created:async function(){
    await fetch('https://restcountries.eu/rest/v2/name/'+this.$route.params.country+'?fullText=true').then((res)=>res.json())
    .then(data=>{
      if(data.status==404){
        this.showError=true;
        this.country=[];
      }else{
        this.country=data[0];
        console.log(this.country);
        this.showError=false;
      }
    })
  }
}
</script>
